<template>
  <el-container class="StudentSystemContainer">
    <el-header class="StudentSystemHeader">
      <el-radio-group v-model="isCollapse">
        <el-radio-button :value="false" @click="a">expand</el-radio-button>
        <el-radio-button :value="true" @click="b">collapse</el-radio-button>
      </el-radio-group>
    </el-header>

    <el-aside class="StudentSystemAside">
      <el-menu
        active-text-color="#ffd04b"
        background-color="#606266"
        border="none"
        class="el-menu-vertical-demo"
        text-color="#f0f2f5"
        default-active="2"
        :collapse="isCollapse"
      >
        <!-- router default-active="$route.path" -->
        <!-- collapse是菜单是否折叠收起 -->
        <el-menu-item index="1" class="One">
          <el-icon @click="aa"><User /></el-icon>
          <template #title><span>个人信息</span></template>
        </el-menu-item>

        <el-sub-menu index="2">
          <template #title>
            <el-icon><document /></el-icon>
            <span>历史成绩单</span>
          </template>

          <el-menu-item-group>
            <el-menu-item index="2-1" class="Two-One">班级排名</el-menu-item>
            <el-menu-item index="2-2" class="Two-Two">学院排名</el-menu-item>
          </el-menu-item-group>
        </el-sub-menu>

        <el-sub-menu index="3">
          <template #title>
            <el-icon><Histogram /></el-icon>
            <span>学习进度</span>
          </template>

          <el-menu-item-group>
            <el-menu-item index="3-1" class="Three-One">个人进度</el-menu-item>
            <el-menu-item index="3-2" class="Three-Two">学院进度</el-menu-item>
             <el-menu-item index="3-3" class="Three-Three">综合排名</el-menu-item>
          </el-menu-item-group>
        </el-sub-menu>

        <el-menu-item index="4" class="Four">
          <el-icon><ChatDotSquare /></el-icon>
          <template #title><span>系统消息</span></template>
        </el-menu-item>
      </el-menu>
    </el-aside>

    <el-main class="StudentSystemMain">Main</el-main>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      isCollapse: true,
    };
  },
  methods: {
    a() {
      const StudentSystemMain = document.querySelector(".StudentSystemMain");
      StudentSystemMain.style.width = "80%";
      StudentSystemMain.style.left = "20%";
    },
    b() {
      const StudentSystemMain = document.querySelector(".StudentSystemMain");
      StudentSystemMain.style.width = "95%";
      StudentSystemMain.style.left = "5%";
      StudentSystemMain.style.transition = "0.3s";
    },
    aa(){
      this. isCollapse=false;
    }
  },
};
</script>

<style>
.One:focus,
.Two-Two:focus,
.Two-One:focus,
.Three-One:focus,
.Three-Two:focus,
.Three-Three:focus,
.Four:focus {
  background-color: #303133;
}
.el-menu .el-menu-item .el-icon,
.el-menu .el-sub-menu .el-icon {
  color: black !important;
}
.el-menu-vertical-demo {
  background-color: #f0f2f5;
  position: absolute;
  top: 0%;
  min-height: 100%;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  background-color: #606266;
  position: absolute;
  width: 100%;
  top: 0%;
  min-height: 100%;
}
.StudentSystemMain {
  background-color: rgb(179, 74, 74);
  /* rgb(232.8, 233.4, 234.6); */
  position: absolute;
  top: 12%;
  left: 20%;
  width: 80%;
  height: 88%;
}
.StudentSystemAside {
  background-color: rgb(255, 255, 255);
  position: absolute;
  top: 12%;
  left: 0%;
  width: 20%;
  height: 88%;
}

.StudentSystemHeader {
  background-color: #303133;
  position: absolute;
  top: 0%;
  width: 100%;
  height: 12%;
}
.StudentSystemContainer {
  background-color: rgb(232.8, 233.4, 234.6);
  display: grid;
  place-items: center;
  position: relative;
  width: 99vw;
  height: 97vh;
}
</style>
